.repository-item_root {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  height: 60px;
  padding: 10px 0;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-transition-property: background-color;
  transition-property: background-color;
  -webkit-transition-duration: .2s;
          transition-duration: .2s;
  border-top: 1px solid #F6F6F6;
}

.repository-item_root:last-child {
  border-bottom: 1px solid #F6F6F6;
}

.repository-item_root:hover {
  background-color: #F5FAFF;
}

.repository-item_avatar {
  margin-left: 15px;
  width: 30px;
  height: 30px;
}

.repository-item_loginName {
  margin-left: 45px;
  -ms-flex-preferred-size: 120px;
      flex-basis: 120px;
}

.repository-item_main {
  margin-left: 45px;
  -webkit-box-flex: 1;
      -ms-flex: auto;
          flex: auto;
}
